body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  background-color: #ffffff;
  min-width: 320px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-v13-latin-regular.woff2") format("woff2"), url("../fonts/montserrat-v13-latin-regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v13-latin-600.woff2") format("woff2"), url("../fonts/montserrat-v13-latin-600.woff") format("woff"); }

.landing {
  background-image: url("../img/background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-bottom: 100px;
  padding-top: 100px; }

.landing__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  color: #111111;
  margin: 0;
  padding: 0 20px 30px; }

.landing__slogan {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #111111;
  margin: 0;
  padding: 0 20px; }

.main-nav {
  position: relative; }

.main-nav__wrapper {
  display: flex;
  align-items: center;
  padding: 0 20px;
  min-height: 65px; }

.main-nav__list {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #111111;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  text-align: center; }

.main-nav__item {
  text-decoration: none;
  display: block;
  padding: 20px 0 20px 0;
  text-transform: uppercase;
  border-bottom: 1px solid #e6e6e6; }
  .main-nav__item--active {
    border-bottom: 2px solid #69caf7; }
  .main-nav__item:first-child {
    border-top: 1px solid #e6e6e6; }

.main-nav__toggle {
  border: 0;
  background-color: #ffffff; }

.main-nav--opened .main-nav__toggle {
  cursor: pointer;
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 2;
  width: 18px;
  height: 18px;
  border: none;
  background-color: #ffffff; }

.main-nav--closed .main-nav__toggle {
  cursor: pointer;
  position: absolute;
  top: 22px;
  right: 20px;
  z-index: 2;
  width: 24px;
  height: 18px;
  border: none; }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 18px;
    background-image: url(../img/icon-mobile-menu.svg); }

.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 1px;
  width: 18px;
  height: 18px;
  background-image: url(../img/icon-close.svg); }

@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none; } }

.main-nav__logo {
  display: flex;
  align-items: center; }

.main-nav__logo-title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  text-transform: lowercase;
  max-width: 120px; }

.link--landing {
  margin-top: 52px;
  background-color: #0078B3;
  padding: 12px 25px 12px 25px;
  color: white;
  text-transform: uppercase;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto; }

.stock-card {
  border: 1px solid #f2f2f2; }

.stock-card__desc {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #111111;
  margin: 0;
  padding: 0 40px 60px 40px;
  text-align: center; }

.stock-card__icon-wrapper {
  text-align: center;
  padding-top: 20px; }

.stock-card__link {
  display: block;
  margin-top: 40px; }

.stock__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 30px;
  line-height: 32px;
  color: #111111;
  margin: 20px 0 0 0;
  padding: 0 20px;
  display: inline-block;
  position: relative;
  background-color: #ffffff; }
  .stock__title::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 100vw;
    left: 0;
    height: 3px;
    margin-top: 2px;
    background: #69caf7;
    z-index: -1; }

.stock__list {
  list-style: none;
  margin: 0;
  padding: 30px 0 0 0;
  display: flex;
  flex-wrap: wrap; }

.stock__item {
  margin: 0 20px 20px;
  width: 100%; }
